import { useState } from "react";
import { IoCheckmarkDone, IoCloseOutline } from "react-icons/io5";
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/black-and-white.css";
// import required modules
import { useEffect } from "react";
import ContentLoader from "react-content-loader";
import { useHistory } from "react-router-dom";
import { Navigation } from "swiper";
import http from "../api";
import { limitText, PriceNumber } from "../utils";

//sample data

export default function UnUsedCar({ title }) {
  const api = http();
  const history = useHistory();
  const [data, setData] = useState();
  const [loaded, setLoaded] = useState(false);
  useEffect(() => {
    api.GetLastUnUsedCarList(({ data }) => {
      setData(data);
      setLoaded(true);
    });
  }, []);

  if (!loaded) {
    return (
      <div className="row mt-lg-5 mt-2 justify-content-center mb-4">
        <ContentLoader
          backgroundColor={"#fff"}
          foregroundColor={"#e4e4e4e4"}
          viewBox="0 0 380 70"
        >
          {/* Only SVG shapes */}
          <rect x="0" y="0" rx="5" ry="5" width="60" height="70" />
          <rect x="70" y="0" rx="5" ry="5" width="60" height="70" />
          <rect x="150" y="0" rx="5" ry="5" width="60" height="70" />
          <rect x="230" y="0" rx="5" ry="5" width="60" height="70" />
          <rect x="310" y="0" rx="5" ry="5" width="60" height="70" />
        </ContentLoader>
      </div>
    );
  }
  return (
    <div className="col-lg-12 mt-3 w-100">
      <div className="d-flex flex-row align-items-center justify-content-between">
        <span className="title-text" style={{ fontSize: 17 }}>
          {title}
        </span>
        <div className="dash" />
        <p
          className="note-text tx-link"
          style={{ cursor: "pointer" }}
          onClick={() => history.push(`/unUsedCars`, { title })}
        >
          مشاهده همه
        </p>
      </div>

      <Swiper
        // slidesPerView={3}

        modules={[Navigation]}
        // navigation={true}
        breakpoints={{
          320: {
            slidesPerView: 1.2,
            spaceBetween: 10,
          },
          // when window width is >= 480px
          480: {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          // when window width is >= 640px
          640: {
            slidesPerView: 2,
            spaceBetween: 40,
          },
          720: {
            slidesPerView: 3,
            spaceBetween: 40,
          },
          960: {
            slidesPerView: 4,
            spaceBetween: 40,
          },
          1200: {
            slidesPerView: 5,
            spaceBetween: 40,
          },
        }}
        className="new-car-slider-container pb-0"
      >
        {data.map((item, key) => (
          <SwiperSlide
            className="unUsedCar-slider-item shadow"
            key={key}
            onClick={() => history.push("/unUsedCars/Detail/" + item.id)}
          >
            <LazyLoadImage
              width={"100%"}
              height={180}
              src={`${
                process.env.NODE_ENV == "development"
                  ? "https://localhost:7045"
                  : "https://mashinto.com"
              }/media/unUsedCar/${
                item.images.find((c) => c.isFeatureImage)?.imageSrc
              }`}
              effect="black-and-white"
              alt={item.name}
              style={{ maxHeight: 180 }}
              // onError={({ currentTarget }) => {
              //   currentTarget.onerror = null; // prevents looping
              //   currentTarget.src =
              //     "https://mashinto.com/media/ads/defualt.png";
              //   currentTarget.style = "max-height:180px";
              // }}
            />

            <h1 className="title-text my-3 mx-2">{limitText(item.name, 20)}</h1>

            <div className="d-flex flex-row align-items-center justify-content-between px-2 mt-3">
              <div className="d-flex flex-row align-items-center ">
                {item.isAvailable ? (
                  <IoCheckmarkDone className="car-new-icon" color="green" />
                ) : (
                  <IoCloseOutline className="car-new-icon" color="red" />
                )}
                <span className="new-car-text">
                  {item.isAvailable ? "موجود" : "نا موجود"}
                </span>
              </div>
              <div className="d-flex flex-row align-items-center "></div>
            </div>

            <div className="d-flex flex-row align-items-center justify-content-between px-2 mt-2 ">
              <div className="d-flex flex-row align-items-center "></div>
              <span className="title-text">
                قیمت :
                {item.priceType == 0
                  ? " تماس بگیرید"
                  : PriceNumber(item.finalPrice) + "   تومان"}
              </span>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
}
